.clock__hands {
  width: 10px;
  height: 10px;
  border-radius: 100%;
  background: #6C5116;
  box-sizing: border-box;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -5px;
  margin-top: -5px;
  transform: rotate(180deg);
}

.clock__hand {
  transform-origin: top center;
  background: #000;
  position: absolute;
  top: 5px;
  transition: transform 0.2s;
}

.clock__hand:before {
  content: "";
  display: block;
  width: 0;
  height: 0;
  position: absolute;
}

.clock__hand:after {
  content: "";
  display: block;
  position: absolute;
  background: #000;
}

.clock__hand--hour {
  width: 6px;
  height: 60px;
  left: 2px;
}

.clock__hand--hour:before {
  bottom: -8px;
  border: solid transparent;
  border-width: 4px 3px;
  border-top-color: #000;
}

.clock__hand--hour:after {
  width: 6px;
  height: 12px;
  top: -12px;
}

.clock__hand--minute {
  width: 4px;
  height: 80px;
  left: 3px;
}

.clock__hand--minute:before {
  bottom: -6px;
  border: solid transparent;
  border-width: 3px 2px;
  border-top-color: #000;
}

.clock__hand--minute:after {
  width: 4px;
  height: 16px;
  top: -16px;
}

.clock__hand--second {
  width: 1px;
  height: 88px;
  left: 4.5px;
}

.clock__hand--second:before {
  top: -29.04px;
  border: 0;
  width: 3px;
  height: 22px;
  background: #000;
  left: -1px;
}

.clock__hand--second:after {
  width: 1px;
  height: 17.6px;
  top: -17.6px;
}

.cursor-pointer:hover {
  cursor: pointer;
}

.gradient-bar {
  background-image: repeating-linear-gradient(270deg, #9079e1 0, #2cd6aa 10%, #28aff8 20%, #9079e1 30%);
  height: 5px;
}

.event-item span {
  font-size: .9em;
}

.outline-0:focus {
  outline: none;
}

.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: table;
  transition: opacity .3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 40%;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
  transition: all .3s ease;
  text-align: right;
}

.modal-header span {
  margin-top: 0;
}

.modal-body {
  margin: 20px 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

@media (max-width: 768px) {
  .modal-container {
    width: 90%;
    max-height: 400px;
    overflow-y: scroll;
  }
}

#event-details {
  display: none;
  z-index: 999;
  cursor: default;
}

.event-item-title:hover #event-details {
  display: block;
}
